/* YourComponent.css */
.movie-container {
    position: relative;
    margin-right: 10px;
    margin-top: 10px;
    transition: transform 0.2s ease-in-out;
}

.addMovie {
    background-color: red;
    width: 200px;
    height: 40px;
    text-align: center;
    padding: 8px;
    font-size: large;
    text-decoration: none;
    color: white;
    margin: 20px;
    border-radius: 5px;
}
.content {
    position: absolute;
    top: 80%;
    left: 48%;
    transform: translate(-50%, -50%);
    padding: 10px;
    display: flex;
    border-radius: 10px;
    overflow: hidden;
    color: white;
    width: 230px;
    height: 97px;
}

.movie-container:hover {
    transform: scale(1.01);
}

.movie-container:hover .content {
    color: white;
    display: block;
    width: 230px;
    height: 97px;
    background-color: rgba(0, 0, 0, 0.8);
}

.ant-input-search-button {
    background-color: red;
}